button.lock {
  background: transparent;
  border: 0;
  padding: 0;
}

[data-locked='true'] input,
[data-locked='true'] select {
  background: #000;
  color: #777;
  border: 0;
}

[data-locked='true'] button.delete {
  display: none !important;
}

[data-locked='true'] .ability input[type='radio']:checked + label {
  background: #222;
}

*::-webkit-scrollbar {
  width: 0px;
  height: 0px;
}

.icon svg:hover {
  opacity: 0.8;
}

.textfield::-webkit-outer-spin-button,
.textfield[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

input[type='number'] {
  text-align: center;
}
